@import "normalize.css";
@import "bootstrap-grid.min.css";
@import "bootstrap.min.css";
@import "components/common.scss";
@import "components/variables.scss";
@import "components/footer.scss";
@import "components/header.scss";
@import "components/hero.scss";
@import "components/price.scss";
@import "components/about-us.scss";
@import "components/advantages.scss";
@import "components/price-calc.scss";
@import "components/about-hotel.scss";
@import "components/reviews.scss";
@import "components/ring-past.scss";

@font-face{
  font-family: 'Montserrat';
  src: url(../fonts/Montserrat-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face{
  font-family: 'Montserrat';
  src: url(../fonts/Montserrat-Black.ttf) format("truetype");
  font-weight: 900;
  font-style: normal;
}

@font-face{
  font-family: 'Montserrat';
  src: url(../fonts/Montserrat-Medium.ttf) format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face{
  font-family: 'Montserrat';
  src: url(../fonts/Montserrat-Bold.ttf) format("truetype");
  font-weight: bold;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  color: #0c0c0c;
  font-family: 'Montserrat', sans-serif;
}

/* Базовые стили, начало*/

img {
  display: block;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
}

a {
  color: #0c0c0c;
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
  transition: all 0.2s ease;
}

ul {
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
  color: #0c0c0c;
  font-size: 12px;
}

h1 {
  display: none;
}

h2 {
  font-size: 26px;
  font-weight: 900;
}

h3 {
  font-size: 22px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 10px;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  color: #0c0c0c;
}

/* Базовые стили, конец */

.main, .header {
  margin: 0 auto;
  max-width: 1920px;
}

@media (max-width: 1140px) {
  h2 {
      font-size: 24px;
  }
}

@media (max-width: 960px) {
  h2 {
      font-size: 20px;
  }

  h3 {
      font-size: 16px;
  }
}

